.home-page {
  font-family: Arial, sans-serif;
  padding-top: 50px;
}

.navbar {
  background-color: #8dbae7; 
  padding: 10px 20px; 
  height: 15%;       
  display: flex;              
  align-items: center;        
  position: fixed;            
  top: 0;                     
  left: 0;                    
  width: 100%;                
  z-index: 1000;             
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); 
  margin: 0;                 
  border-bottom: 1px solid #ccc; 
}

.navbar .search-form {
  margin-right: auto; 
}

.navbar ul {
  list-style-type: none;      
  margin: 0;                  
  padding: 0;                 
  display: flex;              
}

.navbar li {
  margin: 0 15px;            
  position: relative;
}

.navbar a {
  text-decoration: none;      
  color: #333;              
  padding: 5px 10px;         
  transition: color 0.3s ease; 
}

.navbar a:hover {
  color: #007bff;
  text-decoration: underline; 
}




.search-form {
  display: flex;
  margin-left: 50%;
}

.search-form input {
  padding: 5px;
  border: 1px solid #ccc; 
}

.search-form button {
  padding: 5px;
  border: 1px solid #007bff; 
  background-color: #007bff; 
  color: white;              
  cursor: pointer;           
  transition: background-color 0.3s ease; 
}

.search-form button:hover {
  background-color: #0056b3; 
}

.carousel {
  height: 80vh; /* Use viewport height for better responsiveness */
  margin: 100px 0;
  overflow: hidden; /* Prevents overflow if images are larger */
}

.carousel-image {
  width: 100%; /* Make the image take full width of the container */
  height: auto; /* Maintain aspect ratio */
  object-fit: cover; /* Cover the entire area while keeping aspect ratio */
  margin-bottom: 10px; 
}



.product-container {
  padding: 20px;
}

.product-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr)); 
  gap: 90px;
}

.product-item {
  display: flex;
  flex-direction: column; 
  justify-content: space-between; 
  height: 100%; 
  margin: 10px 20px 30px 20px;
}
.price {
  color: #333; 
  font-size: 18px; 
  text-align: center; 
  margin-bottom: 10px; 
  width: 100px; 
  margin-left: auto; 
  margin-right: auto; 
}




.product-image {
  width: 100%;
  height: auto;
  transition: transform 0.3s ease; 
}

.product-image:hover {
  transform: scale(1.05); 
}

.price-and-button {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.price-and-button p {
  margin: 0;
}

.order-now-button {
  background-color: #ff9900;
  color: #fff;
  padding: 10px 20px; 
  border: none;
  cursor: pointer;
  border-radius: 5px;
  margin-top: auto; 
  font-size: 16px; 
  width: 100%; 
  max-width: 200px; 
}

.order-now-button:hover {
  background-color: #ff7700;
}



.footer {
  background-color: #333; 
  color: white;           
  text-align: center;     
  padding: 30px;          
  position: relative;      
  bottom: 0;              
  width: 100%;            
}

.footer p {
  margin: 0;              
  font-size: 14px;        
}

.footer a {
  color: #f1c40f;         
  text-decoration: none;   
}

.footer a:hover {
  text-decoration: underline;
}

.social-icons {
  display: flex;
  justify-content: center;
  margin-bottom: 10px; 
}

.icon {
  width: 24px;
  height: 24px;
  margin: 0 10px; 
}
.popup {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(255, 255, 255, 0.9);
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  z-index: 1000; /* Ensure it's on top */
}

.popup-content {
  display: flex;
  flex-direction: column;
  align-items: center; /* Center the icon and text */
}

.truck-icon {
  width: 30px; 
  height: 20px; 
  background-color: #4CAF50; 
  position: relative; 
  margin-bottom: 10px; 
}

.truck-icon::before {
  content: '';
  position: absolute;
  width: 15px; 
  height: 15px; 
  background-color: #2E7D32; 
  top: -10px; 
  left: 5px; 
}

.truck-icon::after {
  content: '';
  position: absolute;
  bottom: -5px; 
  left: 5px; 
  width: 20px; 
  height: 5px; 
  background-color: #000; 
}





