.App {
  display: flex;
  justify-content: flex-start; 
  align-items: center;
  min-height: 100vh;
  background-color: #f0f2f5;
  background: radial-gradient(circle, rgba(238,174,202,1) 0%, rgba(148,187,233,1) 100%);
  background-size: cover;
  padding-left: 500px;
}

.container {
  display: flex;
  flex-direction: column;
}

h1 {
  font-size: 2.5rem;
  color: #0a0909;
  margin-bottom: 30px;
  text-align: center;
}

.form-container {
  font-weight: bold;
  color: #000000;
  width: 400px;
  padding: 30px;
  background-color: rgba(255, 255, 255, 0.6); 
  border-radius: 15px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
  text-align: center;
  background-image: linear-gradient(0deg, rgba(255, 222, 233, 0.6) 0%, rgba(181, 255, 252, 0.6) 100%);
  overflow: hidden;
}

label {
  display: block;
  margin-bottom: 8px;
  font-size: 1rem;
  color: inherit;
  margin-bottom: 15px;
}

input[type="text"],
input[type="email"],
input[type="password"] {
  width: 100%;
  padding: 10px;
  font-size: 1rem;
  border: 1px solid #ddd;
  border-radius: 5px;
  box-sizing: border-box;
  transition: border-color 0.3s ease;
  color: #0a0909; 
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="password"]:focus {
  border-color: #007bff; 
  outline: none; 
}

.btn-submit {
  width: 100%;
  padding: 10px;
  background-color: #31b3b8b7; 
  color: white;
  border: none; 
  border-radius: 4px;
  font-size: 1.4rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin-top: 25px;
}

.btn-submit:hover {
  background-color: #0056b3; 
}

.signup-link, .forgot-password-link {
  margin-top: 15px;
  text-align: center;
  font-size: 0.9rem;
  display: block;
  color: #007bff;
  cursor: pointer;
  text-decoration: none;
}

.signup-link {
  margin-top: 10px;
}

.signup-link:hover, .forgot-password-link:hover {
  text-decoration: underline;
}

.form-group {
  margin-bottom: 10px;
  font-size: 0.8rem;
  margin-top: 10px;
}
